body {
  margin: 0;
}

a {
  color: white;
}

ul {
  margin: 0;
  padding: 0;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: darkorange;
  padding: 8px 12px;
}

.navbar__logo {
  font-size: 24px;
  padding: 10px 24px;
  background-color: aqua;
}

.navbar__menu {
  display: flex;
  list-style: none;
  padding-left: 0;
}

.navbar__menu li {
  padding: 8px 12px;
}

.navbar__menu li:hover {
  background-color: blue;
  border-radius: 4px;
}

.navbar__toogleBtn {
  display: none;
  position: absolute;
  right: 0;
  font-size: 24px;
  color: rebeccapurple;
  background-color: brown;
}

.navbar__toogleBtn:hover {
  background-color: red;
}

@media screen and (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
  }

  .navbar__menu {
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: aquamarine;
  }

  .navbar__menu li {
    width: 100%;
    text-align: center;
  }

  .navbar__menu li:hover {
    background-color: blue;
    border-radius: 0;
  }

  .navbar__toogleBtn {
    display: block;
    padding: 5px 10px;
  }
}
